import React from 'react';
import {
    HashRouter as Router,
    Route,Link,Redirect
  } from 'react-router-dom'
import history from './../history';
import {FaThumbsUp, FaMap} from 'react-icons/fa'  
import {db} from '../firebase'
import './list.css'
import MyAppBar from '../AppComponents/AppBar.js'
import Images from '../AppComponents/Images.js'
import TopPicks from '../AppComponents/topPicks.js'
import ClassesDisplay from './display';

  var length;
  var n;
  class ClassList extends React.Component{
    state = {
        classes:null,
        visible:false,
        name: ""
      }

      componentDidMount(){
        const data = db.collection('Classes');    
         data.get()
          .then(snapshot=>{
            const classes = []
            length = snapshot.size;
            snapshot.forEach(doc=>{
              const data = doc.data()
              classes.push(data)
            })
            this.setState({classes:classes})
          })
      }
    
      render(){

          if(this.state.visible){
            return <ClassesDisplay doc_name={n} />
          }

          return(
              <div>
                 <MyAppBar/>
                 <Images/>
                 <TopPicks/>
                <div style={{backgroundColor:'#E6E6E6'}}>
                <div class='number' ><FaThumbsUp size='12' style={{marginRight:'5px'}}/> Found {length} Pidgin classes around you.</div>
                {
                          this.state.classes&&
                          this.state.classes.map(classes=>{
                            n = classes.docName
                            return(

                                <div class='item'>
                                  <div class='showImage'>
                                    <img src={classes.i1} height='200px' style={{marginRight:'2px'}} ></img>
                                    <img src={classes.i2} height='200px'style={{marginRight:'2px'}}></img>
                                  </div>

                                  <button class='itemButton' onClick={()=>{
                                      this.setState({visible:true})
                                    }}>
                                    <div class='container'>
                                  <div class='name'>{classes.name}</div>
                                      <div class='map'>
                                        <a href={classes.location}><FaMap size='13'color='#04BFBF' /></a>
                                        <div>Map</div>
                                        </div>
                                    </div>
                                    
                                      <div class='type'>
                                        {classes.type}
                                      </div>
                                      <div class='type'>
                                        {classes.adress}
                                      </div>
                                      <hr color='#E6E6E6'></hr>
                                      <div class='fees'>
                                        <div class='type'>Starting Fees</div> &#8377;{classes.fees}
                                      </div>
                                    
                                  </button> 
                                </div>
                             
                            )
                          })
                }
                
              </div>
            </div>
          )
      }
  }
  export default ClassList;